<template>
  <div class="instrutor-login">
    <div class="container">
      <div class="login-card">
        <h2>Login do Instrutor</h2>
        
        <div class="tabs">
          <button 
            :class="{ active: activeTab === 'login' }" 
            @click="activeTab = 'login'"
          >
            Fazer Login
          </button>
          <button 
            :class="{ active: activeTab === 'cadastro' }" 
            @click="activeTab = 'cadastro'"
          >
            Cadastrar
          </button>
        </div>
        
        <!-- Tab de Login -->
        <div v-if="activeTab === 'login'" class="tab-content">
          <div class="form-group">
            <label>Selecione o Instrutor:</label>
            <select v-model="selectedInstrutor" class="form-control">
              <option value="">Selecione um instrutor</option>
              <option 
                v-for="instrutor in instrutores" 
                :key="instrutor.id" 
                :value="instrutor"
              >
                {{ instrutor.qra }} - {{ instrutor.nome }}
              </option>
            </select>
          </div>
          
          <button 
            @click="fazerLogin" 
            :disabled="!selectedInstrutor"
            class="btn btn-primary"
          >
            Entrar
          </button>
        </div>
        
        <!-- Tab de Cadastro -->
        <div v-if="activeTab === 'cadastro'" class="tab-content">
          <div class="form-group">
            <label>QRA do Instrutor:</label>
            <input 
              v-model="novoInstrutor.qra" 
              type="text" 
              class="form-control"
              placeholder="Ex: PY2ABC"
            />
          </div>
          
          <div class="form-group">
            <label>Nome do Instrutor:</label>
            <input 
              v-model="novoInstrutor.nome" 
              type="text" 
              class="form-control"
              placeholder="Nome completo"
            />
          </div>
          
          <button 
            @click="cadastrarInstrutor" 
            :disabled="!novoInstrutor.qra || !novoInstrutor.nome"
            class="btn btn-success"
          >
            Cadastrar
          </button>
        </div>
        
        <div class="back-link">
          <router-link to="/">‚Üê Voltar ao in√≠cio</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { instrutorService } from '../services/api'

export default {
  name: 'InstrutorLogin',
  data() {
    return {
      activeTab: 'login',
      instrutores: [],
      selectedInstrutor: null,
      novoInstrutor: {
        qra: '',
        nome: ''
      }
    }
  },
  
  async mounted() {
    await this.carregarInstrutores()
  },
  
  methods: {
    async carregarInstrutores() {
      try {
        this.instrutores = await instrutorService.getAll()
      } catch (error) {
        console.error('Erro ao carregar instrutores:', error)
        alert('Erro ao carregar instrutores')
      }
    },
    
    fazerLogin() {
      if (this.selectedInstrutor) {
        // Salvar instrutor no localStorage
        localStorage.setItem('instrutor', JSON.stringify(this.selectedInstrutor))
        this.$router.push('/definir-provas')
      }
    },
    
    async cadastrarInstrutor() {
      try {
        await instrutorService.create(this.novoInstrutor)
        alert('Instrutor cadastrado com sucesso!')
        
        // Limpar formul√°rio
        this.novoInstrutor = { qra: '', nome: '' }
        
        // Recarregar lista de instrutores
        await this.carregarInstrutores()
        
        // Mudar para tab de login
        this.activeTab = 'login'
      } catch (error) {
        console.error('Erro ao cadastrar instrutor:', error)
        alert('Erro ao cadastrar instrutor: ' + (error.response?.data?.error || error.message))
      }
    }
  }
}
</script>

