<template>
  <div class="definir-provas">
    <div class="container">
      <header class="page-header">
        <h1>Definir Provas</h1>
        <p v-if="instrutorLogado">
          Instrutor: <strong>{{ instrutorLogado.qra }} - {{ instrutorLogado.nome }}</strong>
        </p>
      </header>
      
      <div class="content-grid">
        <!-- Formul√°rio de Nova Prova -->
        <div class="form-card">
          <h2>Nova Prova</h2>
          
          <div class="form-group">
            <label>Nome da Prova:</label>
            <input 
              v-model="novaProva.nome" 
              type="text" 
              class="form-control"
              placeholder="Ex: Prova de Matem√°tica - 1¬∫ Bimestre"
            />
          </div>
          
          <div class="form-group">
            <label>Data de Aplica√ß√£o:</label>
            <input 
              v-model="novaProva.data_aplicacao" 
              type="date" 
              class="form-control"
            />
          </div>
          
          <button 
            @click="criarProva" 
            :disabled="!novaProva.nome || !novaProva.data_aplicacao"
            class="btn btn-primary"
          >
            Criar Prova
          </button>
        </div>
        
        <!-- Lista de Provas -->
        <div class="provas-card">
          <h2>Provas Cadastradas</h2>
          
          <div v-if="provas.length === 0" class="empty-state">
            <p>Nenhuma prova cadastrada ainda.</p>
          </div>
          
          <div v-else class="provas-list">
            <div 
              v-for="prova in provas" 
              :key="prova.id" 
              class="prova-item"
            >
              <div class="prova-info">
                <h3>{{ prova.nome }}</h3>
                <p>Data: {{ formatarData(prova.data_aplicacao) }}</p>
                <p>Instrutor: {{ prova.instrutor?.qra }} - {{ prova.instrutor?.nome }}</p>
              </div>
              
              <div class="prova-actions">
                <button 
                  @click="editarProva(prova)" 
                  class="btn btn-sm btn-secondary"
                >
                  Editar
                </button>
                <button 
                  @click="excluirProva(prova.id)" 
                  class="btn btn-sm btn-danger"
                >
                  Excluir
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <!-- Modal de Edi√ß√£o -->
      <div v-if="provaEditando" class="modal-overlay" @click="cancelarEdicao">
        <div class="modal" @click.stop>
          <h2>Editar Prova</h2>
          
          <div class="form-group">
            <label>Nome da Prova:</label>
            <input 
              v-model="provaEditando.nome" 
              type="text" 
              class="form-control"
            />
          </div>
          
          <div class="form-group">
            <label>Data de Aplica√ß√£o:</label>
            <input 
              v-model="provaEditando.data_aplicacao" 
              type="date" 
              class="form-control"
            />
          </div>
          
          <div class="modal-actions">
            <button @click="salvarEdicao" class="btn btn-primary">
              Salvar
            </button>
            <button @click="cancelarEdicao" class="btn btn-secondary">
              Cancelar
            </button>
          </div>
        </div>
      </div>
      
      <div class="navigation">
        <router-link to="/" class="btn btn-outline">‚Üê In√≠cio</router-link>
        <router-link to="/aplicar-provas" class="btn btn-primary">Aplicar Provas ‚Üí</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { provaService } from '../services/api'

export default {
  name: 'DefinirProvas',
  data() {
    return {
      instrutorLogado: null,
      provas: [],
      novaProva: {
        nome: '',
        data_aplicacao: ''
      },
      provaEditando: null
    }
  },
  
  async mounted() {
    this.verificarLogin()
    await this.carregarProvas()
  },
  
  methods: {
    verificarLogin() {
      const instrutor = localStorage.getItem('instrutor')
      if (instrutor) {
        this.instrutorLogado = JSON.parse(instrutor)
      } else {
        this.$router.push('/login')
      }
    },
    
    async carregarProvas() {
      try {
        if (this.instrutorLogado) {
          this.provas = await provaService.getByInstrutor(this.instrutorLogado.id)
        }
      } catch (error) {
        console.error('Erro ao carregar provas:', error)
        alert('Erro ao carregar provas')
      }
    },
    
    async criarProva() {
      try {
        const prova = {
          ...this.novaProva,
          instrutor_id: this.instrutorLogado.id
        }
        
        await provaService.create(prova)
        alert('Prova criada com sucesso!')
        
        // Limpar formul√°rio
        this.novaProva = { nome: '', data_aplicacao: '' }
        
        // Recarregar lista
        await this.carregarProvas()
      } catch (error) {
        console.error('Erro ao criar prova:', error)
        alert('Erro ao criar prova: ' + (error.response?.data?.error || error.message))
      }
    },
    
    editarProva(prova) {
      this.provaEditando = { ...prova }
    },
    
    async salvarEdicao() {
      try {
        await provaService.update(this.provaEditando.id, {
          nome: this.provaEditando.nome,
          data_aplicacao: this.provaEditando.data_aplicacao
        })
        
        alert('Prova atualizada com sucesso!')
        this.provaEditando = null
        await this.carregarProvas()
      } catch (error) {
        console.error('Erro ao atualizar prova:', error)
        alert('Erro ao atualizar prova: ' + (error.response?.data?.error || error.message))
      }
    },
    
    cancelarEdicao() {
      this.provaEditando = null
    },
    
    async excluirProva(provaId) {
      if (confirm('Tem certeza que deseja excluir esta prova?')) {
        try {
          await provaService.delete(provaId)
          alert('Prova exclu√≠da com sucesso!')
          await this.carregarProvas()
        } catch (error) {
          console.error('Erro ao excluir prova:', error)
          alert('Erro ao excluir prova: ' + (error.response?.data?.error || error.message))
        }
      }
    },
    
    formatarData(data) {
      return new Date(data).toLocaleDateString('pt-BR')
    }
  }
}
</script>

