<template>
  <div class="aplicar-provas">
    <div class="container">
      <header class="page-header">
        <h1>Aplicar Provas</h1>
        <p v-if="instrutorLogado">
          Instrutor: <strong>{{ instrutorLogado.qra }} - {{ instrutorLogado.nome }}</strong>
        </p>
      </header>
      
      <div class="form-card">
        <h2>Registrar Pontua√ß√µes</h2>
        
        <div class="form-group">
          <label>Selecione a Prova:</label>
          <select v-model="provaSelecionada" class="form-control" @change="limparPontuacoes">
            <option value="">Selecione uma prova</option>
            <option 
              v-for="prova in provas" 
              :key="prova.id" 
              :value="prova"
            >
              {{ prova.nome }} - {{ formatarData(prova.data_aplicacao) }}
            </option>
          </select>
        </div>
        
        <div v-if="provaSelecionada" class="pontuacoes-section">
          <div class="section-header">
            <h3>Pontua√ß√µes dos Alunos</h3>
            <button @click="adicionarLinha" class="btn btn-success btn-sm">
              + Adicionar Aluno
            </button>
          </div>
          
          <div class="pontuacoes-list">
            <div 
              v-for="(pontuacao, index) in pontuacoes" 
              :key="index"
              class="pontuacao-item"
            >
              <div class="pontuacao-fields">
                <div class="field-group">
                  <label>QRA do Aluno:</label>
                  <input 
                    v-model="pontuacao.qra_aluno" 
                    type="text" 
                    class="form-control"
                    placeholder="Ex: PY2XYZ"
                  />
                </div>
                
                <div class="field-group">
                  <label>Pontua√ß√£o:</label>
                  <input 
                    v-model.number="pontuacao.pontuacao" 
                    type="number" 
                    step="0.1"
                    min="0"
                    max="10"
                    class="form-control"
                    placeholder="0.0"
                  />
                </div>
                
                <div class="field-actions">
                  <button 
                    @click="removerLinha(index)" 
                    class="btn btn-danger btn-sm"
                    :disabled="pontuacoes.length === 1"
                  >
                    üóëÔ∏è
                  </button>
                </div>
              </div>
            </div>
          </div>
          
          <div class="form-actions">
            <button 
              @click="salvarPontuacoes" 
              :disabled="!pontuacoesValidas"
              class="btn btn-primary"
            >
              Salvar Pontua√ß√µes
            </button>
            
            <button 
              @click="limparPontuacoes" 
              class="btn btn-secondary"
            >
              Limpar Tudo
            </button>
          </div>
          
          <!-- Pontua√ß√µes j√° registradas -->
          <div v-if="pontuacoesExistentes.length > 0" class="pontuacoes-existentes">
            <h3>Pontua√ß√µes j√° Registradas</h3>
            <div class="pontuacoes-registradas">
              <div 
                v-for="pontuacao in pontuacoesExistentes" 
                :key="pontuacao.id"
                class="pontuacao-registrada"
              >
                <span class="qra">{{ pontuacao.qra_aluno }}</span>
                <span class="pontuacao">{{ pontuacao.pontuacao }}</span>
                <button 
                  @click="editarPontuacao(pontuacao)" 
                  class="btn btn-sm btn-secondary"
                >
                  Editar
                </button>
                <button 
                  @click="excluirPontuacao(pontuacao.id)" 
                  class="btn btn-sm btn-danger"
                >
                  Excluir
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <!-- Modal de Edi√ß√£o -->
      <div v-if="pontuacaoEditando" class="modal-overlay" @click="cancelarEdicao">
        <div class="modal" @click.stop>
          <h2>Editar Pontua√ß√£o</h2>
          
          <div class="form-group">
            <label>QRA do Aluno:</label>
            <input 
              v-model="pontuacaoEditando.qra_aluno" 
              type="text" 
              class="form-control"
            />
          </div>
          
          <div class="form-group">
            <label>Pontua√ß√£o:</label>
            <input 
              v-model.number="pontuacaoEditando.pontuacao" 
              type="number" 
              step="0.1"
              min="0"
              max="10"
              class="form-control"
            />
          </div>
          
          <div class="modal-actions">
            <button @click="salvarEdicao" class="btn btn-primary">
              Salvar
            </button>
            <button @click="cancelarEdicao" class="btn btn-secondary">
              Cancelar
            </button>
          </div>
        </div>
      </div>
      
      <div class="navigation">
        <router-link to="/definir-provas" class="btn btn-outline">‚Üê Definir Provas</router-link>
        <router-link to="/resultados" class="btn btn-primary">Ver Resultados ‚Üí</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { provaService, pontuacaoService } from '../services/api'

export default {
  name: 'AplicarProvas',
  data() {
    return {
      instrutorLogado: null,
      provas: [],
      provaSelecionada: null,
      pontuacoes: [
        { qra_aluno: '', pontuacao: null }
      ],
      pontuacoesExistentes: [],
      pontuacaoEditando: null
    }
  },
  
  computed: {
    pontuacoesValidas() {
      return this.pontuacoes.every(p => 
        p.qra_aluno.trim() !== '' && 
        p.pontuacao !== null && 
        p.pontuacao !== '' &&
        p.pontuacao >= 0 &&
        p.pontuacao <= 10
      )
    }
  },
  
  async mounted() {
    this.verificarLogin()
    await this.carregarProvas()
  },
  
  methods: {
    verificarLogin() {
      const instrutor = localStorage.getItem('instrutor')
      if (instrutor) {
        this.instrutorLogado = JSON.parse(instrutor)
      } else {
        this.$router.push('/login')
      }
    },
    
    async carregarProvas() {
      try {
        if (this.instrutorLogado) {
          this.provas = await provaService.getByInstrutor(this.instrutorLogado.id)
        }
      } catch (error) {
        console.error('Erro ao carregar provas:', error)
        alert('Erro ao carregar provas')
      }
    },
    
    async carregarPontuacoesExistentes() {
      if (this.provaSelecionada) {
        try {
          this.pontuacoesExistentes = await pontuacaoService.getByProva(this.provaSelecionada.id)
        } catch (error) {
          console.error('Erro ao carregar pontua√ß√µes existentes:', error)
        }
      }
    },
    
    adicionarLinha() {
      this.pontuacoes.push({ qra_aluno: '', pontuacao: null })
    },
    
    removerLinha(index) {
      if (this.pontuacoes.length > 1) {
        this.pontuacoes.splice(index, 1)
      }
    },
    
    limparPontuacoes() {
      this.pontuacoes = [{ qra_aluno: '', pontuacao: null }]
      if (this.provaSelecionada) {
        this.carregarPontuacoesExistentes()
      }
    },
    
    async salvarPontuacoes() {
      try {
        const pontuacoesParaSalvar = this.pontuacoes.map(p => ({
          qra_aluno: p.qra_aluno.trim(),
          pontuacao: parseFloat(p.pontuacao),
          prova_id: this.provaSelecionada.id
        }))
        
        await pontuacaoService.createLote(pontuacoesParaSalvar)
        alert('Pontua√ß√µes salvas com sucesso!')
        
        // Limpar formul√°rio e recarregar pontua√ß√µes existentes
        this.limparPontuacoes()
      } catch (error) {
        console.error('Erro ao salvar pontua√ß√µes:', error)
        alert('Erro ao salvar pontua√ß√µes: ' + (error.response?.data?.error || error.message))
      }
    },
    
    editarPontuacao(pontuacao) {
      this.pontuacaoEditando = { ...pontuacao }
    },
    
    async salvarEdicao() {
      try {
        await pontuacaoService.update(this.pontuacaoEditando.id, {
          qra_aluno: this.pontuacaoEditando.qra_aluno,
          pontuacao: this.pontuacaoEditando.pontuacao
        })
        
        alert('Pontua√ß√£o atualizada com sucesso!')
        this.pontuacaoEditando = null
        await this.carregarPontuacoesExistentes()
      } catch (error) {
        console.error('Erro ao atualizar pontua√ß√£o:', error)
        alert('Erro ao atualizar pontua√ß√£o: ' + (error.response?.data?.error || error.message))
      }
    },
    
    cancelarEdicao() {
      this.pontuacaoEditando = null
    },
    
    async excluirPontuacao(pontuacaoId) {
      if (confirm('Tem certeza que deseja excluir esta pontua√ß√£o?')) {
        try {
          await pontuacaoService.delete(pontuacaoId)
          alert('Pontua√ß√£o exclu√≠da com sucesso!')
          await this.carregarPontuacoesExistentes()
        } catch (error) {
          console.error('Erro ao excluir pontua√ß√£o:', error)
          alert('Erro ao excluir pontua√ß√£o: ' + (error.response?.data?.error || error.message))
        }
      }
    },
    
    formatarData(data) {
      return new Date(data).toLocaleDateString('pt-BR')
    }
  },
  
  watch: {
    provaSelecionada() {
      if (this.provaSelecionada) {
        this.carregarPontuacoesExistentes()
      }
    }
  }
}
</script>

