import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import InstrutorLogin from '../views/InstrutorLogin.vue'
import DefinirProvas from '../views/DefinirProvas.vue'
import AplicarProvas from '../views/AplicarProvas.vue'
import Resultados from '../views/Resultados.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'InstrutorLogin',
    component: InstrutorLogin
  },
  {
    path: '/definir-provas',
    name: 'DefinirProvas',
    component: DefinirProvas
  },
  {
    path: '/aplicar-provas',
    name: 'AplicarProvas',
    component: AplicarProvas
  },
  {
    path: '/resultados',
    name: 'Resultados',
    component: Resultados
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router

