<template>
  <div class="resultados">
    <div class="container">
      <header class="page-header">
        <h1>Resultados das Provas</h1>
        <p>Visualize os resultados e estat√≠sticas dos alunos</p>
      </header>
      
      <div class="content-grid">
        <!-- Busca por Aluno Espec√≠fico -->
        <div class="search-card">
          <h2>Buscar Aluno Espec√≠fico</h2>
          
          <div class="form-group">
            <label>QRA do Aluno:</label>
            <div class="search-input">
              <input 
                v-model="qraBusca" 
                type="text" 
                class="form-control"
                placeholder="Ex: PY2XYZ"
                @keyup.enter="buscarAluno"
              />
              <button 
                @click="buscarAluno" 
                :disabled="!qraBusca.trim()"
                class="btn btn-primary"
              >
                Buscar
              </button>
            </div>
          </div>
          
          <!-- Resultado da Busca Individual -->
          <div v-if="resultadoIndividual" class="resultado-individual">
            <h3>Resultado de {{ resultadoIndividual.qra_aluno }}</h3>
            
            <div class="stats-grid">
              <div class="stat-item">
                <span class="stat-label">Total de Pontos:</span>
                <span class="stat-value total">{{ resultadoIndividual.total_pontuacao }}</span>
              </div>
              
              <div class="stat-item">
                <span class="stat-label">Total de Provas:</span>
                <span class="stat-value">{{ resultadoIndividual.total_provas }}</span>
              </div>
              
              <div class="stat-item">
                <span class="stat-label">M√©dia:</span>
                <span class="stat-value media">{{ resultadoIndividual.media }}</span>
              </div>
            </div>
            
            <div class="detalhes-provas">
              <h4>Detalhes por Prova:</h4>
              <div class="provas-detalhes">
                <div 
                  v-for="prova in resultadoIndividual.detalhes_provas" 
                  :key="prova.prova_id"
                  class="prova-detalhe"
                >
                  <div class="prova-info">
                    <strong>{{ prova.nome_prova }}</strong>
                    <span class="data">{{ formatarData(prova.data_aplicacao) }}</span>
                  </div>
                  <div class="prova-pontuacao">
                    {{ prova.pontuacao }}
                  </div>
                </div>
              </div>
            </div>
          </div>
          
          <div v-if="erroIndividual" class="erro-busca">
            <p>{{ erroIndividual }}</p>
          </div>
        </div>
        
        <!-- Ranking Geral -->
        <div class="ranking-card">
          <h2>Ranking Geral</h2>
          
          <div class="ranking-controls">
            <button @click="carregarRanking" class="btn btn-secondary">
              üîÑ Atualizar
            </button>
          </div>
          
          <div v-if="ranking.length === 0" class="empty-state">
            <p>Nenhum resultado encontrado.</p>
          </div>
          
          <div v-else class="ranking-list">
            <div 
              v-for="(resultado, index) in ranking" 
              :key="resultado.qra_aluno"
              class="ranking-item"
              :class="{ 
                'primeiro': index === 0, 
                'segundo': index === 1, 
                'terceiro': index === 2 
              }"
            >
              <div class="posicao">
                <span class="numero">{{ index + 1 }}¬∫</span>
                <span v-if="index === 0" class="medal">ü•á</span>
                <span v-else-if="index === 1" class="medal">ü•à</span>
                <span v-else-if="index === 2" class="medal">ü•â</span>
              </div>
              
              <div class="aluno-info">
                <strong>{{ resultado.qra_aluno }}</strong>
              </div>
              
              <div class="aluno-stats">
                <div class="stat">
                  <span class="label">Total:</span>
                  <span class="value">{{ resultado.total_pontuacao }}</span>
                </div>
                <div class="stat">
                  <span class="label">Provas:</span>
                  <span class="value">{{ resultado.total_provas }}</span>
                </div>
                <div class="stat">
                  <span class="label">M√©dia:</span>
                  <span class="value">{{ resultado.media }}</span>
                </div>
              </div>
              
              <div class="acoes">
                <button 
                  @click="verDetalhes(resultado.qra_aluno)" 
                  class="btn btn-sm btn-outline"
                >
                  Ver Detalhes
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <div class="navigation">
        <router-link to="/aplicar-provas" class="btn btn-outline">‚Üê Aplicar Provas</router-link>
        <router-link to="/" class="btn btn-primary">In√≠cio</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { resultadoService } from '../services/api'

export default {
  name: 'ResultadosView',
  data() {
    return {
      qraBusca: '',
      resultadoIndividual: null,
      erroIndividual: null,
      ranking: []
    }
  },
  
  async mounted() {
    await this.carregarRanking()
  },
  
  methods: {
    async buscarAluno() {
      if (!this.qraBusca.trim()) return
      
      try {
        this.erroIndividual = null
        this.resultadoIndividual = await resultadoService.getByAluno(this.qraBusca.trim())
      } catch (error) {
        console.error('Erro ao buscar aluno:', error)
        this.resultadoIndividual = null
        this.erroIndividual = error.response?.data?.error || 'Aluno n√£o encontrado'
      }
    },
    
    async carregarRanking() {
      try {
        this.ranking = await resultadoService.getTodosAlunos()
      } catch (error) {
        console.error('Erro ao carregar ranking:', error)
        alert('Erro ao carregar ranking')
      }
    },
    
    verDetalhes(qraAluno) {
      this.qraBusca = qraAluno
      this.buscarAluno()
      
      // Scroll para a se√ß√£o de busca
      document.querySelector('.search-card').scrollIntoView({ 
        behavior: 'smooth' 
      })
    },
    
    formatarData(data) {
      return new Date(data).toLocaleDateString('pt-BR')
    }
  }
}
</script>

